.list {
    display: grid;
    max-width: calc(100vw - 48px);
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    grid-gap: 16px;
    margin-top: 0;
    margin-bottom: 0;
    padding: 0;
    list-style: none;
    margin-left: auto;
    margin-right: auto;
}

.text{
    padding: 5px 10px;
    font-size: 16px;
    text-align: center;
    font-weight: bold;
}
.img{
    width: 100%;
    height: 460px;
    object-fit: cover;
}